html, body { height: 100%; }

html { font-size: 62.5%; }

body { background: #fff; color: #636363; font-family: "Montserrat", sans-serif; font-size: 1.4rem; line-height: 2.2rem; box-sizing: border-box; }

button, select, label[for], input[type='submit'], input[type='image'] { color: inherit; cursor: pointer; outline: none !important; }

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button { -webkit-appearance: none; margin: 0; }

input[type='number'] { -moz-appearance: textfield; }

input::-ms-clear { display: none; }

:disabled, [disabled], [aria-disabled="true"] { cursor: not-allowed; }

a { color: inherit; text-decoration: none; transition: all 0.2s linear; }

a:hover { text-decoration: none; }

a:focus { color: inherit; outline: none; text-decoration: none; }

strong { font-weight: 700; }

b { font-weight: 600; }

input, select, button, textarea { font: inherit; outline: none; }

img { height: auto; max-width: 100%; }

.clearfix:after { content: ""; display: table; clear: both; }

@-webkit-keyframes scroll { 0% { -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  100% { -webkit-transform: translate(-100%, 0);
    transform: translate(-100%, 0); } }

@-moz-keyframes scroll { 0% { -moz-transform: translate(0, 0);
    transform: translate(0, 0); }
  100% { -moz-transform: translate(-100%, 0);
    transform: translate(-100%, 0); } }

@keyframes scroll { 0% { transform: translate(0, 0); }
  100% { transform: translate(-100%, 0); } }

header { background: transparent; height: 10rem; margin-bottom: -10rem; min-width: 320px; order: 1; width: 100%; z-index: 999; }

header .wrapper { display: flex; align-items: center; justify-content: space-between; height: 100%; }

header .wrapper:after { content: none; }

header .site-menu { display: flex; }

header .contact-link { border-bottom: 1px solid; color: #fff; font-weight: 600; letter-spacing: 2px; margin-right: 3.5rem; padding: 1.5rem 0; text-transform: uppercase; }

header nav { align-items: center; cursor: pointer; display: flex; }

header nav ul { display: none; }

/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer { background: #2A0E42; min-width: 320px; order: 3; width: 100%; }

footer ul { list-style: none; }

footer .widget .widget-title { color: #4f4f4f; font-weight: 600; letter-spacing: 2px; margin-bottom: 2rem; text-transform: uppercase; }

footer .widget li { margin-bottom: 1rem; }

footer .widget li.current { color: #8800f6; list-style: disc; }

footer .widget li.current a { color: #8800f6; }

footer .widget a { color: #ededed; }

footer .widget a:hover { color: #8800f6; }

footer .footer-social { align-items: center; display: flex; flex-wrap: wrap; float: none; justify-content: space-between; margin: 8rem auto 0; max-width: 65rem; padding-bottom: 6.5rem; text-align: center; }

footer .footer-social a { display: inline-block; }

footer .footer-social a:hover svg { fill: #fff; }

footer .footer-social a svg { fill: #520596; }

footer .copyright { border-top: 2px solid #520596; display: flex; align-items: center; justify-content: center; color: #520596; height: 9rem; font-size: 1.4rem; font-weight: 600; letter-spacing: 2px; line-height: 9rem; text-transform: uppercase; }

footer .copyright span { position: relative; }

footer .copyright span:before, footer .copyright span:after { background: #520596; border-radius: 50%; height: 3px; content: ''; display: inline-block; margin: auto 5px; vertical-align: middle; width: 3px; }

.main-wrapper { padding: 0 0 0 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: flex; flex-direction: column; justify-content: flex-start; }

.wrapper { min-width: 320px; max-width: 1410px; margin: 0 auto; position: relative; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

/* loader */
.loaded .main-wrapper { visibility: hidden; }

.icon-load { background: url(../img/loader.gif) no-repeat left top; width: 4rem; height: 4rem; position: fixed; left: 50%; top: 50%; margin-left: -2rem; display: none; }

.loaded .icon-load { display: block; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content { flex-grow: 1; min-width: 320px; order: 2; text-align: left; width: 100%; }

.button { background: #8A00F4; border: 3px solid #8A00F4; color: #fff; display: block; font-size: 1.4rem; font-weight: 600; height: 7.7rem; line-height: 7.7rem; letter-spacing: 2px; max-width: 51rem; min-width: 32rem; position: relative; text-align: center; text-transform: uppercase; transition: all 0.2s linear; }

.button:hover { background: transparent; color: #8A00F4; }

.button:hover .icon-arrow.violet { opacity: 1; }

.button:hover .icon-arrow.white { opacity: 0; }

.button .icon-arrow { bottom: 0; margin: auto 0 auto 2rem; position: absolute; top: 0; }

.button .icon-arrow.violet { opacity: 0; }

.navigation-button { cursor: pointer; position: relative; }

.navigation-button, .navigation-button::before, .navigation-button::after { background-color: #fff; display: inline-block; height: 2px; width: 5.1rem; }

.navigation-button::before, .navigation-button::after { content: ""; left: 0; position: absolute; transition: all .2s; width: 4.2rem; }

.navigation-button::before { top: -.8rem; }

.navigation-button::after { top: .8rem; }

nav:hover .navigation-button:before { top: -1rem; }

nav:hover .navigation-button:after { top: 1rem; }

nav .navigation-button.active { background-color: transparent; }

nav .navigation-button.active:before { top: 0; transform: rotate(135deg); }

nav .navigation-button.active:after { top: 0; transform: rotate(-135deg); }

form { font-family: "Open Sans", sans-serif; padding-top: 4.3rem; }

