form .checkbox label:after { background: #fff; border-radius: 50%; box-shadow: 2px 2px 15px -5px rgba(0, 0, 0, 0.5), -2px -2px 15px -5px rgba(0, 0, 0, 0.5); content: ''; height: 100%; left: -1px; position: absolute; transition: all 0.2s linear; width: 3rem; }

