#slider.ui-slider:before { background-image: -webkit-linear-gradient(90deg, #d1d1d1 1px, transparent 0); background-image: linear-gradient(90deg, #d1d1d1 1px, transparent 0); background-size: 28px 39px; background-repeat: repeat-x; background-position: 1rem bottom; content: ''; height: 62px; left: 0; position: absolute; top: -38px; width: 100%; }

